.navbarr {
  background-color: rgba(0, 0, 0, 0.904);
  padding: 0.5rem 3rem;
  height: 4rem;
  display: flex;
  align-items: center;
  justify-content: center;
}
.nav-icon {
  color: white;
  font-family: 'Poppins', sans-serif;
  margin-right: 3.8rem;
  border-right: 1.5px solid whitesmoke;
  padding-right: 2rem;
  font-size: 15px;
  text-decoration: none;
}
.icon1:hover {
  color: #ff6767;
  text-decoration: none;
}
.icon2:hover {
  color: #feb641;
  text-decoration: none;
}
.icon3:hover {
  color: #fdfd97;
