@import url(https://fonts.googleapis.com/css2?family=Vollkorn:wght@500&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Roboto+Condensed:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:ital@1&family=Poppins:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:ital@1&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}


.ttt {
  margin-left: 26rem;
  width: 350px;
}
.tweet {
  font-family: 'Vollkorn', serif;
  text-shadow: 1px 1px 1px #110303;

}
.day{
  padding: 0.5rem 3rem;
  background-color:   #ff6767;
}
.pic1{
  width:200px;
  margin-right: 1rem;
  font-family: 'Roboto Condensed', sans-serif;
}
.lgbtq{
  /* display: flex; */
  padding: 0.5rem 3rem;
  background-color:  #feb641;

}
.header {
  display: flex;
  background-color: black;
  justify-content: space-between;
  padding: 1.5rem 3rem;
}
.prider {
  background-image: linear-gradient(
    to left,
    violet,
    indigo,
    blue,
    green,
    yellow,
    orange,
    red
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  letter-spacing: 8px;
  font-size: 25px;
  font-weight: 600;
  /* border: 7px solid orange; */
  /* border-radius: 5px; */
  font-family: 'Montserrat', sans-serif;
  padding: 6px 6px 6px 6px;
  
  margin:0 auto;
  font-weight: 700;
  font-size: 34px;
}
.header-icon {
  margin-right: 17px;
  font-size: 23px;
  color: whitesmoke;
}
.header-icon:hover {
  color: orange;
}
.header-btn {
  margin-left: 80px;
  background-color: orange;
  font-weight: 600 !important;
  font-size: 19px !important;
  padding: 10px 15px !important;
}
.header-right {
  display: flex;
  align-items: center; /* align item is used to position on y axis*/
}

/* @import url('https://fonts.googleapis.com/css2?family=Roboto:wght@100&display=swap'); */

